/* üî• ÍπúÎπ°ÏûÑ Î∞©ÏßÄ: ÌÖåÎßà Ï†ÑÌôòÏù¥ Ï¶âÏãú Ï†ÅÏö©ÎêòÎèÑÎ°ù ÏÑ§Ï†ï */
html {
  /* üî• Ï§ëÏöî: color-scheme ÏÑ§Ï†ïÏúºÎ°ú Î∏åÎùºÏö∞Ï†Ä Í∏∞Î≥∏ ÎèôÏûë Ï†úÏñ¥ */
  color-scheme: light dark;
}

/* üî• CSS Î≥ÄÏàò Ï†ïÏùò - Light/Dark ÌÖåÎßà */
:root {
  /* üî• Í∏∞Í∞ÄÏ∞®Îìú ÏûëÍ∞Ä ÏπúÌôîÏ†Å ÏÉâÏÉÅ ÏãúÏä§ÌÖú - ÎùºÏù¥Ìä∏ Î™®Îìú */
  
  /* Í∏∞Î≥∏ Î∞∞Í≤Ω - Îî∞ÎúªÌïú ÌÅ¨Î¶ºÏÉâ (Ï¢ÖÏù¥ ÎäêÎÇå) */
  --bg-primary: #fefcf7;
  --bg-secondary: #f8f6f0; 
  --bg-elevated: #ffffff;
  --bg-card: #ffffff;
  --bg-hover: #f3f4f6;
  
  /* ÌÖçÏä§Ìä∏ - ÏßÑÌïú Ï∞®ÏΩú (ÏàúÏàò Í≤ÄÏ†ï ÎåÄÏã†) */
  --text-primary: #1a1a1a;
  --text-secondary: #4a4a4a;
  --text-muted: #737373;
  --text-inverse: #ffffff;
  
  /* Í∞ïÏ°∞ÏÉâ - Ï∞®Î∂ÑÌïú Î∏îÎ£® */
  --accent-primary: #2563eb;
  --accent-hover: #1d4ed8;
  --accent-light: #dbeafe;
  --accent-dark: #1e40af;
  
  /* Í≤ΩÍ≥ÑÏÑ† - Î∂ÄÎìúÎü¨Ïö¥ ÌöåÏÉâ */
  --border-light: #e5e7eb;
  --border-medium: #d1d5db;
  --border-dark: #9ca3af;
  
  /* ÏÉÅÌÉú ÏÉâÏÉÅ - ÏûêÏó∞Ïä§Îü¨Ïö¥ ÌÜ§ */
  --success: #059669;
  --success-light: #d1fae5;
  --warning: #d97706;
  --warning-light: #fed7aa;
  --error: #dc2626;
  --error-light: #fee2e2;
  
  /* Í∑∏Î¶ºÏûê */
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1);
  
  /* ÏûëÍ∞Ä ÏπúÌôîÏ†Å Ìè∞Ìä∏ Ïä§ÌÉù */
