/* üî• Í∏∞Î≥∏ ÏöîÏÜå Ïä§ÌÉÄÏùº Î¶¨ÏÖã */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
  font-size: 16px;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  height: 100%;
  font-family: var(--font-primary);
  background-color: var(--bg-primary);
  color: var(--text-primary);
  overflow-x: hidden;
  /* üî• ÍπúÎπ°ÏûÑ Î∞©ÏßÄ: ÌÖåÎßà Ï†ÑÌôò Ïï†ÎãàÎ©îÏù¥ÏÖò Ï†úÍ±∞ */
  transition: none;
}

/* üî• Îã§ÌÅ¨ Î™®Îìú body Ïä§ÌÉÄÏùº (Ï¶âÏãú Ï†ÅÏö©) */
html.dark body,
[data-theme="dark"] body {
