}

/* üî• Focus Ring Ïª§Ïä§ÌÑ∞ÎßàÏù¥Ïßï */
*:focus {
  outline: 2px solid var(--color-primary-500);
  outline-offset: 2px;
}

*:focus:not(:focus-visible) {
  outline: none;
}

