  display: block;
}

/* üî• List Í∏∞Î≥∏ Ïä§ÌÉÄÏùº Î¶¨ÏÖã */
ul, ol {
  list-style: none;
}

/* üî• Typography Ïä§ÌÉÄÏùº */
h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
  line-height: 1.25;
  margin: 0;
}

p {
  margin: 0;
  line-height: 1.6;
}

/* üî• Ïª¥Ìè¨ÎÑåÌä∏Î≥Ñ Ïú†Ìã∏Î¶¨Ìã∞ ÌÅ¥ÎûòÏä§ */

/* Container */
.container {
  width: 100%;
  max-width: var(--max-width-container);
  margin: 0 auto;
  padding: 0 var(--spacing-lg);
}

/* Flexbox Ïú†Ìã∏Î¶¨Ìã∞ */
.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.flex-start {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.flex-end {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

/* Grid Ïú†Ìã∏Î¶¨Ìã∞ */
.grid-auto-fit {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--spacing-lg);
}

.grid-auto-fill {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: var(--spacing-lg);
}

/* Text Ïú†Ìã∏Î¶¨Ìã∞ */
.text-truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.text-balance {
  text-wrap: balance;
}

/* Animation Ïú†Ìã∏Î¶¨Ìã∞ */
.animate-fade-in {
  animation: fadeIn var(--duration-normal) ease-in-out;
}

.animate-slide-up {
  animation: slideUp var(--duration-normal) ease-out;
}

.animate-slide-down {
  animation: slideDown var(--duration-normal) ease-out;
}

.animate-scale-in {
  animation: scaleIn var(--duration-fast) ease-out;
}

/* Keyframes */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slideDown {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0.95);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

.animate-shimmer {
  animation: shimmer 2s ease-in-out infinite;
}

